.categories_sec ul.cat_list{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:1fr 1fr;justify-content:center;grid-gap:1rem}@media only screen and (max-width: 767px){.categories_sec ul.cat_list{grid-template-columns:repeat(1, 1fr)}}.categories_sec ul.cat_list li{justify-content:center;align-items:center;flex-direction:column}.categories_sec ul.cat_list li a{display:block;height:100%;position:relative}.categories_sec ul.cat_list li:first-child{grid-row:1/3;grid-column:1/2}.categories_sec ul.cat_list li .img_wrap{overflow:hidden}.categories_sec ul.cat_list li .img_wrap img{object-fit:cover;height:100%;width:100%;display:block;transition:all .5s ease-in-out}.categories_sec ul.cat_list li .img_wrap img:hover{transform:scale(1.1)}.categories_sec ul.cat_list li .cat_name{position:absolute;bottom:0;left:0;width:100%;background-color:#d4bd5f;color:#fff;margin:0;padding:10px 20px;display:block}.category_list .inner_{display:flex;gap:30px}.category_list .inner_ .product_list{width:80%}